<template>
  <div class="onlineCustomerService">
    <div class="header">
      <div class="search-input">
        <el-input class="input" v-model="queryForm.searchContent" placeholder="请输入手机号码"></el-input>
      </div>
      <div class="button-group">
        <el-button class="button button1" @click="clientList">
          {{
          "客户列表(" +
          (this.clientListData.tableData
          ? this.pageConfig.total
          : 0) +
          "户)"
          }}
        </el-button>
        <el-button class="button button2" @click="query">查询</el-button>
      </div>
    </div>
    <div class="content">
      <div class="info">
        <el-row>
          <el-col :span="24">
            <div class="info-item">
              <span class="prop">用户编号：</span>
              <span class="value">{{ baseInfoData.clientId }}</span>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <div class="info-item">
              <span class="prop">用户名称：</span>
              <span class="value">{{ baseInfoData.name }}</span>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <div class="info-item">
              <span class="prop">用户地址：</span>
              <span class="value">{{ baseInfoData.address }}</span>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">用户类型：</span>
              <span class="value">{{ baseInfoData.category }}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">用电类别：</span>
              <span class="value">{{ baseInfoData.electricityType }}</span>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <div class="info-item">
              <span class="prop">联系电话：</span>
              <span class="value">{{ baseInfoData.telphone }}</span>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">是否现货客户：</span>
              <span class="value">{{ baseInfoData.spotCustomer }}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">是否重点关注客户：</span>
              <span class="value">{{ baseInfoData.focusCustomers }}</span>
            </div>
          </el-col>
        </el-row>
        <el-row style="border: 0">
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">当期电费：</span>
              <span
                class="value"
                :class="
                  baseInfoData.decideCurrentElectricity === 2
                    ? 'false-status'
                    : 'true-status'
                "
              >{{ baseInfoData.currentElectricity||0 + "元" }}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">上期电费：</span>
              <span class="value">
                {{
                baseInfoData.lastPeriodElectricity||0 + "元"
                }}
              </span>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">对比上期电费：</span>
              <span
                class="value"
                :class="
                  baseInfoData.decideDiffLastPeriod === 1
                    ? 'false-status'
                    : 'true-status'
                "
              >{{ baseInfoData.diffLastPeriodElectricity||0 + "元" }}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">电费环比：</span>
              <span
                class="value"
                :class="
                  baseInfoData.decideDiffLastPeriod === 1
                    ? 'false-status'
                    : 'true-status'
                "
              >{{ baseInfoData.mom||0 + "%" }}</span>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">当天是否停电：</span>
              <span
                class="value"
                :class="
                  baseInfoData.decidePowerFailureDay === 2 ? 'false-status' : ''
                "
              >{{ baseInfoData.powerFailureDay }}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">停电工单：</span>
              <span class="value">{{ baseInfoData.blackoutWorkOrder }}</span>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">近30天来电次数：</span>
              <span class="value">{{ baseInfoData2.recentDaysCalls||0 + "次" }}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">在线人工次数：</span>
              <span class="value">{{ baseInfoData2.numOnlineLabor||0 + "次" }}</span>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">最新来电工单号：</span>
              <span class="value">{{ baseInfoData2.latestElectricianNum }}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="info-item">
              <span class="prop">业务子类：</span>
              <span class="value">{{ baseInfoData2.businessSubclass }}</span>
            </div>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <div class="info-item">
              <span class="prop">受理时间：</span>
              <span
                class="value"
                v-if="baseInfoData2.acceptanceTime"
              >{{ baseInfoData2.acceptanceTime}}</span>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="portrait">
        <div class="title">画像标签</div>
        <div class="content">
          <div class="nav">
            <el-checkbox-group v-model="checkList">
              <el-checkbox v-for="item in tagList" :label="item.label" :key="item.id"></el-checkbox>
            </el-checkbox-group>
          </div>
          <div class="tags">
            <span
              class="tag tag1"
              v-show="checkList.indexOf(tagList[0].label)!==-1"
              v-for="item in tag1List"
              :key="item.tagId"
            >{{item.tagName}}</span>
            <span
              class="tag tag2"
              v-show="checkList.indexOf(tagList[1].label)!==-1"
              v-for="item in tag2List"
              :key="item.tagId"
            >{{item.tagName}}</span>
            <span
              class="tag tag3"
              v-show="checkList.indexOf(tagList[2].label)!==-1"
              v-for="item in tag3List"
              :key="item.tagId"
            >{{item.tagName}}</span>
          </div>
        </div>
      </div>
      <div class="manualTag">
        <div class="title">手工标签</div>
        <div class="selectSg">
          <el-select v-model="manualValue" placeholder="请选择" size="mini" @change="changeTagData">
            <el-option
              v-for="item in manualOptions"
              :key="item.tagobjId"
              :label="item.tagobjName"
              :value="item.tagobjId"
            ></el-option>
          </el-select>
        </div>
        <div class="content">
          <span
            v-for="item in sgObjList"
            :key="item.tagId"
            @click="handleSign(item)"
          >{{item.tagName}}</span>
        </div>
      </div>
    </div>
    <el-dialog
      class="dialog"
      title="提示"
      :visible.sync="dialogVisible"
      width="85%"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ dialogTitle }}</span>
      </span>

      <bdaTable
        class="dialog-table"
        v-if="dialogRes"
        :tableData="clientListData"
        :headerColor="dialogHeaderColor"
        :indexWidth="0"
        :isPage="true"
        :pageConfig="pageConfig"
        @clickRow="clickRow"
        @changePage="changePage"
      ></bdaTable>

      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Interface from "@/base/mixin/online-customer-service";
import bdaTable from "@/components/bda-table/bda-table";

import { TOKEN } from "@/config";

export default {
  components: {
    bdaTable
  },
  name: "onlineCustomerService",
  mixins: [Interface],
  data() {
    return {
      baseInfoForm: {
        telPhone: "",
        clientId: ""
      },
      //基本信息
      baseInfoData: {
        clientId: "",
        name: "",
        address: "",
        category: "",
        electricityType: "",
        telphone: "",
        spotCustomer: "",
        focusCustomers: "",
        currentElectricity: 0,
        lastPeriodElectricity: 0,
        diffLastPeriodElectricity: 0,
        mom: 0,
        powerFailureDay: "",
        blackoutWorkOrder: "",
        recentDaysCalls: "",
        numOnlineLabor: "",
        latestElectricianNum: "",
        businessSubclass: "",
        acceptanceTime: "",
        countClientList: 0,
        arrears: "",
        decidePowerFailureDay: 0,
        decideDiffLastPeriod: 0,
        decideCurrentElectricity: 0 //(1为正常,2为红色)
      },
      //拆分出来的后五个字段recentDaysCalls   numOnlineLabor latestElectricianNum  businessSubclass  acceptanceTime
      baseInfoData2: {
        recentDaysCalls: "",
        numOnlineLabor: "",
        latestElectricianNum: "",
        businessSubclass: "",
        acceptanceTime: ""
      },
      //客户列表查询
      queryForm: {
        searchContent: "",
        current: 1,
        size: 10,
        total: 0
      },
      //分页设置
      pageConfig: {
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //客户列表框
      dialogVisible: false,
      clientListData: {},
      dialogRes: false,
      dialogHeaderColor: "",
      dialogTitle: "",

      //标签画像
      checkList: [], //选中的
      tagList: [], //选中的
      tag1List: [], //号码主体
      tag2List: [], //用户体系
      tag3List: [], //台区主体
      //手工标签
      manualOptions: [{}],
      manualValue: "",
      sgObjList: [],
      numberTag: "", //用户编号
      phoneTag: "" //手机号
    };
  },
  watch: {},
  created() {
    this.getListPortraitTagObj();
    window.addEventListener(
      "message",
      event => {
        if (event.data.type !== "webpackOk") {
          console.log(event, "event");
          this.baseInfoForm.telPhone = event.data.msg
            ? event.data.msg.telPhone
            : "";
          this.baseInfoForm.clientId = event.data.msg
            ? event.data.msg.clientId
            : "";
          if (event.data.msg.telPhone) {
            this.queryForm.searchContent = event.data.msg.telPhone;
            this.getBaseInfoData();
            this.getBaseInfoData2();
            this.getClientList();
          }
        }
      },
      false
    );
  },
  methods: {
    handleSign(item) {
      this.signDialogVisible = true;
      this.$confirm("确认要标记此标签吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.markManualTag({
            orgId: "",
            phone: this.phoneTag || "",
            tagId: item.tagId,
            tagobjId: item.tagobjId,
            token: TOKEN,
            userCode: this.numberTag || ""
          }).then(() => {
            this.getListUserPortraitTag();
            this.$message({
              type: "success",
              message: "标记成功!"
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消标记"
          });
        });
    },
    clickRow(prop, v) {
      window.console.log(prop, "prop");
      window.console.log(v);
      if (v.clientId !== "" && v.clientId !== undefined) {
        this.baseInfoForm.clientId = v.clientId;
      }
      if (this.queryForm.searchContent !== "") {
        this.baseInfoForm.telPhone = this.queryForm.searchContent;
      }
      this.getBaseInfoData();
      this.getBaseInfoData2();
      this.handleClose();
    },
    //查看客户列表
    clientList() {
      this.dialogRes = false;
      this.$nextTick(() => {
        this.dialogHeaderColor = "#5475aa";
        this.dialogTitle = "客户列表";
        this.dialogRes = true;
        this.dialogVisible = true;
      });
    },
    //点击查询客户列表
    query() {
      this.getClientList();
      if (this.queryForm.searchContent !== "") {
        this.baseInfoForm.clientId = "";
        this.baseInfoForm.telPhone = this.queryForm.searchContent;
        this.getBaseInfoData();
        this.getBaseInfoData2();
      }
    },
    //改变分页
    changePage(data) {
      this.queryForm.size = data.size;
      this.queryForm.current = data.current;
      this.getClientList();
    },
    //关闭客户列表
    handleClose() {
      this.dialogVisible = false;
    },
    //获取基本信息
    getBaseInfoData() {
      console.log(this.baseInfoForm, "this.baseInfoForm");
      this.getCustomerPortraitInfo(this.baseInfoForm).then(res => {
        this.$nextTick(() => {
          this.numberTag = res.clientId;
          if (this.queryForm.searchContent.length <= 11) {
            this.phoneTag = this.queryForm.searchContent;
          } else {
            this.phoneTag = res.telphone;
          }
          this.baseInfoData = { ...res };
          this.$forceUpdate();
          this.getListUserPortraitManualTag(); //查手工标记
          this.getListUserPortraitTag(); //画像标签
        });
      });
    },
    //获取基本信息下面五个字段
    getBaseInfoData2() {
      console.log(this.baseInfoForm, "getBaseInfoData2");
      this.getCustomerPortraitByTelPhone({
        telPhone: this.baseInfoForm.telPhone
      }).then(res => {
        this.$nextTick(() => {
          this.baseInfoData2 = { ...res };
          this.$forceUpdate();
        });
      });
    },
    //获取客户列表
    getClientList() {
      let columnNameData = [
        {
          prop: "clientId",
          label: "客户编号",
          align: "center",
          type: "CilckRow"
        },
        { prop: "name", label: "客户名称", align: "center" },
        { prop: "address", label: "地址", align: "center", width: "380px" },
        { prop: "region", label: "地区", align: "center" },
        { prop: "supplyStation", label: "供电所", align: "center" }
      ];
      this.getCustomerList(this.queryForm)
        .then(res => {
          this.pageConfig.total = res.total;
          this.pageConfig.size = this.queryForm.size;
          this.pageConfig.current = this.queryForm.current;
          this.clientListData = {
            tableData: res.records,
            columnNameData: columnNameData
          };
        })
        .catch(err => {
          if (err) {
            this.taskData = {
              tableData: [],
              columnNameData: columnNameData
            };
          }
        });
    },

    //一期集成模块
    ...{
      //画像标签
      //画像标签列表
      getListUserPortraitTag() {
        this.listUserPortraitTag({
          token: TOKEN,
          phone: this.phoneTag,
          userCode: this.numberTag,
          tagobjIds: this.manualValue,
          statuses: 3,
          page: 1,
          rows: 10,
          isPage: true
        }).then(res => {
          this.tag1List = [];
          this.tag2List = [];
          this.tag3List = [];
          for (let i = 0; i < res.length; i++) {
            if (res[i].tagobjId === this.tagList[0].id) {
              //用电台区
              this.tag1List.push(res[i]);
            } else if (res[i].tagobjId === this.tagList[1].id) {
              //来电号码
              this.tag2List.push(res[i]);
            } else if (res[i].tagobjId === this.tagList[2].id) {
              //用户编号
              this.tag3List.push(res[i]);
            }
          }
        });
      },
      //手工标签
      //手工标签下拉框列表数据
      getListPortraitTagObj() {
        this.listPortraitTagObj({ token: TOKEN }).then(res => {
          this.manualValue = res[1].tagobjId; //默认显示手机号
          this.manualOptions = res;
          this.checkList = [];
          this.tagList = [];
          res.forEach(item => {
            this.checkList.push(item.tagobjName);
            this.tagList.push({
              label: item.tagobjName,
              id: item.tagobjId
            });
          });
          this.getListUserPortraitManualTag();
          this.getListUserPortraitTag();
        });
      },
      //获取手工标签查询列表
      getListUserPortraitManualTag() {
        this.listUserPortraitManualTag({
          phone: this.phoneTag,
          userCode: this.numberTag,
          tagobjIds: this.manualValue,
          filterAllowApiWaySign: true,
          token: TOKEN
        }).then(res => {
          this.sgObjList = res;
        });
      },
      changeTagData() {
        this.getListUserPortraitManualTag();
        this.getListUserPortraitTag();
      }
    }
  }
};
</script>

<style scoped lang="scss">
.onlineCustomerService {
  width: 100%;
  height: 100%;
  font-size: 14px;
  .false-status {
    color: #f56c6c;
  }
  .true-status {
    // color: #5475aa;
    color: #409eff;
  }
  .header {
    width: 100%;
    // height: 10%;
    margin-bottom: 20px;
    display: flex;
    .search-input {
      width: 60%;
      .input {
        width: 95%;
      }
    }
    .button-group {
      width: 40%;
      min-width: 222px;
      .button {
        ::v-deep .el-button + .el-button {
          margin: 0;
        }
        background-color: #5475aa;
        color: #fff;
      }
      .button1 {
        width: 65%;
        min-width: 110px;
      }
      .button2 {
        width: 30%;
        min-width: 60px;
      }
    }
  }
  .content {
    height: calc(100% - 60px);

    .el-row {
      min-height: 8%;
      display: flex;
      align-items: center;
      padding: 0 10px;
      box-sizing: border-box;
      border-bottom: 1px solid #d0d0d0;
    }
    .info {
      height: calc(60% - 10px);
      margin-bottom: 0px;
    }
    .title {
      background-image: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
      border-radius: 0 100px 100px 0;
      height: 30px;
      line-height: 30px;
      max-width: 100px;
      font-size: 15px;
      color: #fff;
      padding: 0px 10px;
    }
    .portrait {
      /*height: calc(40% - 10px);*/
      .content {
        .nav {
          padding: 10px 4px;
          ::v-deep .el-checkbox-group {
            & > .is-checked:first-child .el-checkbox__label {
              color: #1fc06b;
            }
            & > .is-checked:nth-child(2) .el-checkbox__label {
              color: #e5a418;
            }
            & > .is-checked:nth-child(3) .el-checkbox__label {
              color: #00abfe;
            }
          }
        }
        .tags {
          margin-bottom: 10px;
          .tag {
            display: inline-block;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            max-width: 170px;
            padding: 4px 8px;
            margin: 2px 4px;
          }
          .tag1 {
            color: #1fc06b;
            border: 1px solid #1fc06b;
            border-radius: 15px;
          }
          .tag2 {
            color: #e5a418;
            border: 1px solid #e5a418;
            border-radius: 15px;
          }
          .tag3 {
            color: #00abfe;
            border: 1px solid #00abfe;
            border-radius: 15px;
          }
        }
      }
    }
    .manualTag {
      position: relative;
      .title {
        background-image: linear-gradient(270deg, #a2cd2f 0%, #93c707 100%);
      }
      .selectSg {
        position: absolute;
        right: 10px;
        top: 2px;
        ::v-deep input {
          border: 0px;
          width: 140px;
        }
        ::v-deep .el-input__inner {
          color: #3e71ff;
        }
      }
      .content {
        display: -webkit-flex;
        display: flex;
        flex-wrap: wrap;
        padding: 4px 6px;
        span {
          display: inline-block;
          width: calc(50% - 10px);
          height: 30px;
          line-height: 30px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          text-align: center;
          background: #f2f2f2;
          margin: 5px;
        }
      }
    }
  }
  .dialog {
    ::v-deep .el-table .warning-row {
      background: #ecf5fd;
    }
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }

    .dialog-table {
      height: 340px;
    }
    .dialog-pagination {
      text-align: center;
      margin: 10px 0;
    }
  }
}
</style>