// CP_PATH, JC_PATH
import {
  CP_PATH,
  JC_PATH
} from '@/config'

// ajax
import ajax from '@/utils/ajax'

/**
 * 客户画像信息
 * @const
 * @type {String}
 * @default  '[CP_PATH]/customerProfileInfoController/customerPortrait'
 */
export const GET_CUSTOMER_PORTRAIT_INFO = CP_PATH + '/customerProfileInfoController/customerPortrait'

/**
 * 客户画像信息
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function getCustomerPortraitInfo(data) {
  return ajax({
    method: 'post',
    headers: {
      "Content-Type": "application/json;charset=utf8"
    },
    data: data,
    url: GET_CUSTOMER_PORTRAIT_INFO
  })
}

/**
 * 客户画像信息下面五个字段
 * @const
 * @type {String}
 * @default  '[CP_PATH]/customerProfileInfoController/customerPortraitByTelPhone'
 */
export const GET_CUSTOMER_PORTRAIT_BY_TEL_PHONE = CP_PATH + '/customerProfileInfoController/customerPortraitByTelPhone'

