/**
 *  在线客服集成接口 mixin module
 *  @module base/mixin/onlineCustomerService
 *  @author coder
 */
import {
  getCustomerPortraitInfo,
  getCustomerPortraitByTelPhone,
  getCustomerList,
  listUserPortraitManualTag,
  listUserPortraitTag,
  listPortraitTagObj,
  markManualTag
} from '@/base/api/online-customer-service'

// mixin
export default {

  /**
   * 在线客服集成接口数据存储载体对象
   * @member onlineCustomerService
   * @property {Array} [list=[]] 获取在线客服集成接口列表响应数据
   * @property {number} [page=null] 获取在线客服集成接口列表响应页码
   * @property {number} [limit=null] 获取在线客服集成接口列表页大小，每页记录条数
   * @property {number} [total=0] 获取在线客服集成接口列表数据记录总条数
   * @property {object} [model=null] 获取在线客服集成接口单条记录
   */
  data() {
    return {
      onlineCustomerService: {
        list: [],
        page: null,
        limit: null,
        total: 0,
        model: null
      }
    }
  },
  methods: {

    /**
     * 客户画像信息
     * @function getCustomerPortraitInfo
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getCustomerPortraitInfo(data) {
      return getCustomerPortraitInfo(data).then(res => {
        return res
      })
    },
    /**
     * 客户画像信息下面五个字段
     * @function getCustomerPortraitByTelPhone
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getCustomerPortraitByTelPhone(data) {
      return getCustomerPortraitByTelPhone(data).then(res => {
        return res
      })
    },
    /**
     * 客户列表信息
     * @function getCustomerList
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    getCustomerList(data) {
      return getCustomerList(data).then(res => {
        return res
      })
    },
    /**
     * 获取手工标签查询列表
     * @function listUserPortraitManualTag
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    listUserPortraitManualTag(data) {
      return listUserPortraitManualTag(data).then(res => {
        return res
      })
    },
    /**
     * 画像标签列表
     * @function listUserPortraitTag
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    listUserPortraitTag(data) {
      return listUserPortraitTag(data).then(res => {
        return res
      })
    },
    /**
     * 手工标签下拉框列表数据
     * @function listPortraitTagObj
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    listPortraitTagObj(data) {
      return listPortraitTagObj(data).then(res => {
        return res
      })
    },
    /**
     * 确认标记标签接口
     * @function markManualTag
     * @param {object} data 请求发送的数据data
     * @returns {Promise} Promise实例
     */
    markManualTag(data) {
      return markManualTag(data).then(res => {
        return res
      })
    }
  }
}
